	import topBar from '../../components/TopBar.vue'
    export default {
		data(){
			return{
				userInfo:{}
			}
		},
		components: {
			topBar
		},
		onLoad() {
			this.getData()
		},
        methods: {
            goPath(url){
				uni.navigateTo({url});
			},
			getData(){
				this.request({
					url:'consumer/my/balances',
				}).then(res => {
					this.userInfo = res.data.result
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '信息获取失败，请稍后重试！',
					});
				})
			}
        }
    }
